.product-item {
  max-width: 25vmin;
  max-height: 50vmin;
  margin: 1rem 4vmin 2rem 4vmin;
}
.product-item-img {
  display: flex;
  margin: 0rem auto 0rem auto;
  max-width: 15vmin;
  width: 100%;
  max-height: 15vmin;
  height: 100%;
  border-radius: 15%;
}
.product-item-text {
  background-color: #e1efe1;
  width: 25vmin;
  height: 31vmin;
  border-radius: 10%;
  display: flex;
  flex-direction: column;
  text-align: center;
  justify-content: center;
}
.product-item-name {
  font-family: "Josefin Sans";
  font-style: normal;
  font-weight: 400;
  font-size: 3vmin;
  line-height: 20px;
  color: #123f0a;
}
.product-item-value {
  width: 80%;
  font-family: "Josefin Sans";
  font-style: normal;
  font-weight: 400;
  font-size: 20px;
  line-height: 20px;
  color: black;
  opacity: 50%;
  border-radius: 1rem;
  margin: 1rem auto 0rem auto;
}
.expiry-date {
  color: black;
  font-size: 2.5vmin;
}
.red {
  color: red;
}
